    /* margin-right: 10px; */
    padding: 0px;
    font-size: 24px;
    /* color:aqua !important; */
    /* color: var(--mainFontColor); */
}
.smallIcon:hover {
    color: var(--mainHoverColor);;
}

@media (max-width: 500px) {
