/* pages/NotFound.module.css */
.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 80px); /* 화면의 전체 높이 */
    text-align: center;
    margin-right: 10%;
    margin-left: 10%;
  }
  
  .heading {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  
  .message {
    font-size: 1.5rem;
    margin-bottom: 2rem;
  }
  
  .link {
    font-size: 1.2rem;
    color: var(--mainFontColor);
    font-weight: 800 !important;
    font-family: Arial, Helvetica, sans-serif;
    text-decoration: none;
    border: 1px solid var(--mainFontColor);
    padding: 0.5rem 1rem;
    border-radius: 5px;
    background-color: #ffffff00;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .link:hover {
    border: 1px solid var(--mainHoverColor);
    color: var(--mainHoverColor);
  }
  
  .buttonContainer {
    display: flex;
    gap: 1rem; /* 버튼 간 간격 추가 */
    margin-bottom: 1rem; /* 아래 요소와의 간격 추가 */
  }